const recordVideoContainer = document.querySelector(".record-container");
const videoPreview = document.querySelector("#js-recordVideo");
const recordButton = document.querySelector("#js-recordButton");

var chunks = [];
let streamObject;
let videoRecord;

const handleVideoData = (event) => {
  const { data: videoFile } = event;
  const link = document.createElement("a");
  const videoElement = document.createElement("video");

  videoElement.src = URL.createObjectURL(videoFile);

  link.href = URL.createObjectURL(videoFile);
  link.download = "recorded.avi";
  document.body.appendChild(link);
  link.click();
  link.remove();
};
const stopRecording = (event) => {
  event.preventDefault();
  if (videoRecord.state == "recording") {
    videoRecord.stop();
    videoPreview.srcObject = null;
  }
  recordButton.removeEventListener("click", stopRecording);
  recordButton.addEventListener("click", getVideo);
  recordButton.innerHTML = "녹화 시작";
};
const startRecording = () => {
  videoRecord = new MediaRecorder(streamObject);
  videoRecord.start();
  videoRecord.addEventListener("dataavailable", handleVideoData);
};

const getVideo = async (event) => {
  event.preventDefault();
  try {
    const stream = await navigator.mediaDevices.getDisplayMedia({
      audio: true,
      video: { width: 1280, height: 720 },
    });
    videoPreview.srcObject = stream;
    streamObject = stream;
    startRecording();
    videoPreview.play();
    videoPreview.muted = true;
    recordButton.innerHTML = "녹화 종료";
  } catch (error) {
    console.log(error);
    recordButton.innerHTML = "녹화 불가";
  } finally {
    recordButton.removeEventListener("click", getVideo);
    recordButton.addEventListener("click", stopRecording);
  }
};

function init() {
  recordButton.addEventListener("click", getVideo);
}

if (recordVideoContainer) {
  init();
}
