import axios from "axios";
import routes from "../../routes";

const commentForm = document.querySelector("#js-commentForm");
const commenttexts = document.querySelector("#js-commentsText");
const commentInputImage = document.querySelector("#js-commentInputImage");
const commentNumber = document.querySelector("#js-commentNumber");
const currentUserName = document.querySelector("#js-currentUserName");

const getCurrentTime = () => {
  const currentTime = new Date();
  return `${String(currentTime.getFullYear()).slice(2)}.${
    currentTime.getMonth() < 10
      ? `0${currentTime.getMonth()}`
      : currentTime.getMonth()
  }.${
    currentTime.getDate() < 10
      ? `0${currentTime.getDate()}`
      : currentTime.getDate()
  } / ${
    currentTime.getHours() < 10
      ? `0${currentTime.getHours()}`
      : currentTime.getHours()
  }:${
    currentTime.getMinutes() < 10
      ? `0${currentTime.getMinutes()}`
      : currentTime.getMinutes()
  }`;
};

const fakeComment = (comment) => {
  // create Elements
  const videoCommentsTextDiv = document.createElement("div");
  const videoCommentsInfo = document.createElement("div");
  const videoCommentsColumn1 = document.createElement("div");
  const videoCommentsColumn2 = document.createElement("div");
  const li = document.createElement("li");
  const img = document.createElement("img");
  const creatorName = document.createElement("span");
  const commentTime = document.createElement("span");
  // add Class
  videoCommentsTextDiv.className = "video__comments-text";
  videoCommentsInfo.className = "video__comments-info";
  videoCommentsColumn1.className = "video__comments-column";
  videoCommentsColumn2.className = "video__comments-column";
  // set Value
  img.src = commentInputImage.src;
  li.innerHTML = comment;
  creatorName.innerHTML = currentUserName.innerHTML;
  commentTime.innerHTML = getCurrentTime();
  // append Child
  videoCommentsInfo.append(creatorName, commentTime);
  videoCommentsColumn2.append(videoCommentsInfo, li);
  videoCommentsColumn1.append(img);
  videoCommentsTextDiv.append(videoCommentsColumn1, videoCommentsColumn2);
  // prepend to ul
  commenttexts.prepend(videoCommentsTextDiv);
  // Num up
  console.dir(commentNumber);
  commentNumber.innerText = parseInt(commentNumber.innerText) + 1;
};

const sendComment = async (comment) => {
  const videoId = window.location.href.split("/videos/")[1];
  const request = await axios({
    method: "POST",
    url: `/api/${videoId}/comment`,
    data: {
      comment: comment,
    },
  });
  fakeComment(comment);
};

const handleSubmit = (event) => {
  console.log("upload!");
  event.preventDefault();
  const commentInput = document.querySelector("#js-commentInput");
  const comment = commentInput.value;
  sendComment(comment);
  commentInput.value = "";
};
function init() {
  commentForm.addEventListener("submit", handleSubmit);
}

if (commentForm) {
  init();
}
