import axios from "axios";

const deleteButtons = document.querySelectorAll(".js-commentDelete");

const handleDeleteButton = async (event) => {
  const commentId = event.target.attributes.comment_id.value;
  console.dir(event.target.parentNode.parentNode);
  const request = await axios({
    method: "POST",
    url: `/api/${commentId}/commentDelete`,
  });
  event.target.parentNode.parentNode.remove();
};

function init() {
  for (let i = 0; i < deleteButtons.length; i++) {
    deleteButtons[i].addEventListener("click", handleDeleteButton);
  }
}

if (deleteButtons) {
  init();
}
